.upload-group {
  position: relative;
  margin-top: 55px;

  .upload-container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: inherit;
    flex-direction: column;

    .upload-box {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      background: #f1f1f1;
      width: 600px;
      height: 400px;
      border-radius: 30px;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);

      /* outline: 2px dashed #454545;
      outline-offset: -50px; */
      &::before {
        content: "";
        position: absolute;
        background: transparent;
        border: #454545 dashed 1.5px;
        border-radius: 30px;
        width: 500px;
        height: 300px;
        z-index: 1;
      }

      img {
        width: 15%;
      }
      .BsCloudUploadFill {
        width: 180px;
        height: 180px;
      }

      .upload-choose {
        margin: 1rem;
        z-index: 2;
        font-size: 25px;

        input {
          width: 0.1px;
          height: 0.1px;
          opacity: 0;
          overflow: hidden;
          position: absolute;
          z-index: -1;
        }
        input + label {
          color: black;
          background-color: transparent;
          display: inline-block;
          /* cursor: pointer; */

          span {
            font-weight: 700;
            cursor: pointer;

            &:hover {
              color: #3fdce6;
            }
          }
        }
        .input:focus + label,
        .input + label:hover {
          background-color: red;
        }
        .input:focus + label {
          outline: 5px dotted #3fdce6;
          /* outline: -webkit-focus-ring-color auto 5px; */
        }
      }
      .upload-info {
        margin: 0.5rem;
        height: 1rem;
        font-size: 15px;
        font-style: italic;
        z-index: 2;
      }
    }

    .upload-desccription {
      margin: 2rem;
      text-align: center;
      font-size: 24px;
      font-weight: 600;
      color: #949494;
    }

    .upload-btn {
      width: 120px;
      height: 48px;
      background: #3fdce6;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
      border-radius: 10px;
      border: none;
      color: #fff;
      font-size: 28px;
      cursor: pointer;
      margin-bottom: 1rem;

      &:hover {
        background: #33cfda;
      }
    }
  }
}

.progress-group {
  .progress-container {
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .progress-bar {
      .progress-bar-indicator {
        margin: 2rem 1rem;
        outline: 1px solid black;
        border-radius: 30px;
        width: 80vw;
        height: 30px;
        padding: 4px;

        .progress-bar-indicator-content {
          width: 100%;
          height: 100%;
          border-radius: 30px;
          background-color: #fed542;
          inset: 0;
          width: 0;
          text-align: center;
        }
      }

      .progress-bar-spinner {
        margin-bottom: 1.2rem;
        width: 70px;
        height: 70px;
        border: 4px solid #f9efd1;
        border-top: 4px solid #fad16b;
        border-radius: 50%;

        /* position: absolute; */
        /* top: 80px;
      margin-left: -24px; */

        -webkit-transition-property: -webkit-transform;
        -webkit-transition-duration: 1.2s;
        -webkit-animation-name: rotate;
        -webkit-animation-iteration-count: infinite;
        -webkit-animation-timing-function: linear;

        -moz-transition-property: -moz-transform;
        -moz-animation-name: rotate;
        -moz-animation-duration: 1.2s;
        -moz-animation-iteration-count: infinite;
        -moz-animation-timing-function: linear;

        transition-property: transform;
        animation-name: rotate;
        animation-duration: 1.2s;
        animation-iteration-count: infinite;
        animation-timing-function: linear;
      }

      /* .progress-bar-indicator {} */
    }

    .progress-description {
      font-size: 28px;
      font-style: italic;
      font-weight: 600;
      color: #949494;
      /* p {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        .element {
          border-radius: 100%;
          border: 2px solid #555;
          margin: calc(4px * 2);
          align-self: flex-end;

          &:nth-child(1) {
            animation: preloader 0.6s ease-in-out alternate infinite;
          }
          &:nth-child(2) {
            animation: preloader 0.6s ease-in-out alternate 0.2s infinite;
          }

          &:nth-child(3) {
            animation: preloader 0.6s ease-in-out alternate 0.4s infinite;
          }
        }
      } */
    }

    .progress-status {
      margin: 3rem 0;
      .progress-counter {
        color: #949494;
        text-align: center;
        font-weight: 500;
        font-size: 20px;
        .progress-counter-timer {
          font-size: 24px;
        }
      }

      .progress-error {
        font-size: 1rem;
        /* .alert */
        .progress-error-descript {
          margin-bottom: 2rem;
          text-align: center;
          font-size: 18px;
          font-weight: 500;
          color: #949494;
        }
      }
    }

    .progress-link {
      color: #949494;
      p {
        text-align: center;
        span {
          text-decoration: underline;
          font-style: italic;
          &:hover {
            color: #3fdce6;
